html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  color: var(--light-text);
  overflow-x: hidden;
}

body a, a:link, a:visited, a:focus, a:hover, a:active, a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

.uppercase {
  text-transform: uppercase;
}

:root {
  --primary: #DA3559;
  --primary-light: #AE2A47;
  --bg-dark: #333333;
  --bg-light: #FFFFFF;
  --dark-text: #333333;
  --light-text: #FFFFFF;
}

:root {
  --font-title: "Poppins", sans-serif;
  --font-body: "IBM Plex Sans", sans-serif;
}

h1, h2, h3 {
  margin-top: 0;
}

nav {
  text-transform: uppercase;
}

.container {
  padding: 0 4rem;
}
.container--dark {
  background: var(--bg-dark);
}
.container--light {
  background: var(--bg-light);
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  background-color: var(--bg-dark);
  color: var(--light-text);
  max-height: 8.4375rem;
  max-width: 82.0625rem;
  margin: 0 auto;
}
.header__nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav__list {
  display: flex;
}
.header__nav-item {
  font-weight: 700;
  font-family: var(--font-title);
  font-size: 1.25rem;
}